@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Slab&display=swap);
*{margin:0;padding:0;font-family:"Roboto",sans-serif;box-sizing:border-box;border:0;outline:none}body{background:url(/static/media/Bg.c248a42a.svg);display:flex;justify-content:center;width:100vw;height:100vh}
.navbar{width:1500px;height:50px;margin-top:40px;display:flex;align-items:center;justify-content:space-between;padding:.5rem 2rem}.navbar ul{list-style-type:none;margin:0;padding:0;display:flex;align-items:center;justify-content:space-evenly;width:600px}.navbar ul li{padding:10px 2rem;border-radius:10px 20px 10px 20px;cursor:pointer;font-weight:600;transition:.3s}.navbar ul li a{display:block;text-decoration:none;color:#000}.navbar ul li:hover{-webkit-transform:translate(-2px, -5px);transform:translate(-2px, -5px);-webkit-filter:grayscale(10%);filter:grayscale(10%);box-shadow:6px 7px 6px rgba(54,38,167,.4)}.navbar ul #btn-login{background-color:#ff6b35}.navbar ul #btn-login a{color:#fff}.navbar ul #btn-login:hover{-webkit-filter:grayscale(0);filter:grayscale(0)}
#intro{width:400px;margin-top:20px;font-size:16px;font-weight:500;letter-spacing:.19em;float:left;margin-right:100px}#cabecalho{color:#000;font-family:Roboto;font-style:normal;font-weight:300;font-size:50px;margin-top:45%;padding:0;font-weight:300;font-size:50px;letter-spacing:.245em;height:100px}#botao{margin-top:32px;color:#fff;background-color:#3626a7;width:219px;height:53px;border-radius:10px 30px;font-family:Roboto Slab;font-style:normal;font-weight:normal;font-size:20px;transition:.3s;cursor:pointer}#botao:hover{-webkit-transform:translate(-2px, -5px);transform:translate(-2px, -5px);-webkit-filter:grayscale(10%);filter:grayscale(10%);box-shadow:6px 7px 6px rgba(54,38,167,.4)}#home-img{width:64px;height:10px}#flex{display:flex}

#navbar{position:absolute;width:300px;height:700px;margin-left:100px;margin-top:50px;color:#fff;background:#3626a7;box-shadow:6px 7px 0px #fff;border-radius:10px 40px}h1{height:39px;padding-left:40px;margin-top:37px;font-family:Roboto Slab;font-style:normal;font-weight:normal;font-size:30px;letter-spacing:.025em;text-transform:uppercase;color:#fff}#lista{margin-top:100px;margin-left:99px}.item{font-family:Roboto Slab;font-style:normal;font-weight:normal;font-size:20px;line-height:33px;align-items:center;letter-spacing:.025em;text-transform:uppercase;padding-left:19px}a{text-decoration:none;color:#fff}#despesas{margin-top:40%;list-style-image:url(/static/media/despesa.3e96ee74.svg)}#estrutura{margin-top:31px;list-style-image:url(/static/media/estrutura.f7ee5e3b.svg)}#receitas{margin-top:31px;list-style-image:url(/static/media/receita.4d1fdbc3.svg)}#municipios{margin-top:31px;list-style-image:url(/static/media/municipio.5a17f8dd.svg)}#content{width:1000px;height:640px;float:right;margin-top:80px;margin-right:10%;padding-left:80px;background:#fff;box-shadow:9px 12px 18px rgba(54,38,167,.6);border-radius:10px 40px}h2{padding-top:50px;padding-left:50px;font-size:32px;font-family:Roboto Slab}#description{margin-left:50px;margin-top:40px;font-family:Roboto;font-size:19px;font-weight:500;padding-right:10px}#link-exemplo{color:#3626a7}#table{margin-top:40px;margin-left:50px}td{margin-right:20px;padding:10px 10px 10px 0px;line-height:1.42857143;vertical-align:top;font-family:Roboto;font-size:18px}
#imagem{width:900px;height:640px;margin-top:5%}#information{position:absolute;width:887px;height:460px;left:1090px;top:200px;border:5px solid #3f3d56;box-sizing:border-box;border-radius:10px 40px;background-image:url(/static/media/wave.fe36bde8.svg);background-position:90% 40%;background-repeat:no-repeat;background-size:855px 514px}a{color:#3626a7}.item-list{font-family:Roboto;font-size:18px;list-style-type:none;margin-top:30px;margin-left:70px;width:2000px;height:20px}
#divLogin{border:1px solid #fff;border-radius:0px 25px;width:30rem;height:20rem;margin:auto;padding-top:45px;position:absolute;left:50%;top:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);text-align:center;background-color:#fff;box-shadow:9px 7px 5px rgba(125,110,255,.575)}#divLogin h3{font-size:40px;font-family:"Roboto Slab",serif}#txtUser,#txtPass{border-bottom:1px solid;width:20rem;height:40px;font-size:20px}#txtUser{background:url(/static/media/user.fe703090.svg) no-repeat scroll 7px 7px;padding-left:40px}#txtPass{background:url(/static/media/password.cb15bc25.svg) no-repeat scroll 7px 7px;padding-left:40px}#btnEntrar{background-color:#fff;width:10rem;height:45px;border:1px solid #000;border-radius:25px;font-size:20px;font-family:"Roboto",sans-serif;margin-top:50px;cursor:pointer}#btnEntrar:hover{box-shadow:0 12px 16px 0 rgba(0,0,0,.24),0 17px 50px 0 rgba(0,0,0,.19)}
