#divLogin {
  border: 1px solid white;
  border-radius: 0px 25px;
  width: 30rem;
  height: 20rem;
  margin: auto;

  padding-top: 45px;

  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);

  text-align: center;
  background-color: white;
  -webkit-box-shadow: 9px 7px 5px rgba(125, 110, 255, 0.575);
  -moz-box-shadow: 9px 7px 5px rgba(125, 110, 255, 0.575);
  box-shadow: 9px 7px 5px rgba(125, 110, 255, 0.575);

  h3 {
    font-size: 40px;
    font-family: "Roboto Slab", serif;
  }
}

#txtUser,
#txtPass {
  border-bottom: 1px solid;
  width: 20rem;
  height: 40px;
  font-size: 20px;
}

#txtUser {
  background: url(../../assets/img/user.svg) no-repeat scroll 7px 7px;
  padding-left: 40px;
}

#txtPass {
  background: url(../../assets/img/password.svg) no-repeat scroll 7px 7px;
  padding-left: 40px;
}

#btnEntrar {
  background-color: white;
  width: 10rem;
  height: 45px;
  border: 1px solid black;
  border-radius: 25px;
  font-size: 20px;
  font-family: "Roboto", sans-serif;
  margin-top: 50px;

  cursor: pointer;
}

#btnEntrar:hover {
  box-shadow: 0 12px 16px 0 rgba(0, 0, 0, 0.24),
    0 17px 50px 0 rgba(0, 0, 0, 0.19);
}
